@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{position:relative;background:linear-gradient(#f00000, #f00000 47%, #222224 47%, #222224 53%, #f0f0f0 53%, #f0f0f0);font-family:montserrat, sans-serif;height:100vh}.background-circle{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);height:20rem;width:20rem;border-radius:50%;border:3rem solid #222224;z-index:-1;background-color:#f0f0f0}.content{padding:3rem;height:100vh;width:100vw;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.header{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.logo-container{width:40rem}.logo-container img{width:100%}.sub-title{font-size:3.2rem;font-weight:600;color:#f0f0f0;-webkit-text-fill-color:#f0f0f0;-webkit-text-stroke:1px black}.game-rules{margin-bottom:2rem;color:#222224;font-size:1.6rem;font-weight:600}.card-container{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:110rem;margin:0 auto}.pokemon-card{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:2rem 1rem;width:21rem;height:27rem;background-color:rgba(34,34,36,0.85);border:2px solid #000;border-radius:8px;box-shadow:5px 1rem 8px #1d1d1db9;cursor:pointer;transition:all 0.15s}.pokemon-card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.pokemon-card__img{max-width:100%;max-height:100%}.pokemon-card__name{position:absolute;bottom:0;padding:1rem;width:100%;background-color:rgba(0,0,0,0.5);text-align:center;font-weight:600;font-size:16px;letter-spacing:2px;text-transform:uppercase;color:white}.score{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-bottom:4rem;color:#222224;font-size:3rem;font-weight:600}

